<template>
    <div class="signup">
        <!--   bar-->
        <header-bar></header-bar>

        <div class="centered-container">
            <md-content class="md-elevation-10">
                <form class="vue-form" @submit.prevent="submit">

                    <v-layout>
                        <v-flex style="margin-right : 3%; margin-bottom : 3%;">
                                <v-text-field
                                        label="ID"
                                        v-model="id"
                                        placeholder="please write email address"
                                        :rules="emailRules"></v-text-field>
                        </v-flex>

                    </v-layout>
                    <v-layout>
                        <v-flex style="margin-right : 3%; margin-bottom : 3%;">
                            <v-text-field
                                    label="password"
                                    v-model="password"
                                    type="password"
                                    placeholder="password"
                                    :rules="passwordRules"
                            ></v-text-field>
                        </v-flex>
                    </v-layout>

                    <v-layout>
                        <v-flex style="margin-right : 3%;">
                            <v-text-field label="" v-model="name" type="text" placeholder="name"></v-text-field>
                        </v-flex>
                        <v-flex>
                            <v-text-field label="" v-model="call" type="tel" placeholder="tel"></v-text-field>
                        </v-flex>
                    </v-layout>

                    <v-layout>
                        <v-flex style="margin-right : 3%;">
                            <v-text-field label="" v-model="nickname" type="text" placeholder="nickname"></v-text-field>
                        </v-flex>
                        <v-flex>
                            <v-text-field label="" v-model="birth" type="number" placeholder="date of birth"></v-text-field>
                        </v-flex>
                    </v-layout>

                    <v-layout>
                        <v-textarea
                                solo
                                v-model="textarea"
                                placeholder="introduce yourself"
                                auto-grow
                        ></v-textarea>
                    </v-layout>
                </form>

                <div class="md-layout-item">
                    <md-field>
                        <label> </label>
                        <md-select v-model="department" name="department" id="department">
                            <md-optgroup label="">
                                <md-option value=""></md-option>
                                <md-option value=""></md-option>
                                <md-option value=""></md-option>
                                <md-option value=""></md-option>
                                <md-option value=""></md-option>
                            </md-optgroup>
                            <md-optgroup label="">
                                <md-option value=""></md-option>
                                <md-option value=""></md-option>
                                <md-option value=""></md-option>
                                <md-option value=""></md-option>
                            </md-optgroup>
                            <md-optgroup label="">
                                <md-option value=""></md-option>
                                <md-option value=""></md-option>
                            </md-optgroup>
                            <md-optgroup label="">
                                <md-option value=""></md-option>
                                <md-option value="e-business ">e-business </md-option>
                            </md-optgroup>
                            <md-optgroup label="">
                                <md-option value=""></md-option>
                                <md-option value=""></md-option>
                            </md-optgroup>
                            <md-optgroup label="">
                                <md-option value=""></md-option>
                                <md-option value=""></md-option>
                            </md-optgroup>
                            <md-optgroup label="">
                                <md-option value=""></md-option>
                            </md-optgroup>
                            <md-optgroup label="">
                                <md-option value=""></md-option>
                            </md-optgroup>
                            <md-optgroup label="">
                                <md-option value=""></md-option>
                            </md-optgroup>
                        </md-select>
                    </md-field>
            </div>

                :to
                :value='{{mem
                <md-option v-for="member in members" value="">mem</md-option>
            <div class="md-layout-item">
                <md-field>
                    <label></label>
                    <md-select v-model="selectedInterest" name="interest" id="interest" multiple>
                        <md-option value="music"></md-option>
                        <md-option value="study">(Study)</md-option>
                        <md-option value="exercise"></md-option>
                        <md-option value="dance"></md-option>
                        <md-option value="movie"> & </md-option>
                        <md-option value="trip"></md-option>
                        <md-option value="contest"></md-option>
                    </md-select>
                </md-field>

                <div class="actions md-layout md-alignment-center">
                    <v-btn raised class="action-button jg" color="blue-grey lighten-1" v-on:click="check=true"></v-btn>
                    <v-btn raised class="action-button jg" color="blue-grey lighten-1" to="/"></v-btn>
                </div>
            </div>
        </md-content>

        <!--alert operation -->
        <md-dialog-alert
            :md-active.sync="fail"
            md-title="ALERT"
            md-content="SignUp Failed"
            md-confirm-text="DONE"/>

        <md-dialog-confirm
            :md-active.sync="check"
            md-title="Check"
            md-content="      ."
            md-confirm-text="Check"
            md-cancel-text="Cancle"
            @md-cancel="onCancel"
            @md-confirm="onCheck" />
        </div>
    </div>
</template>

<script>
    import headerBar from '../../components/header'

    export default {
        name : 'signup',

        components : {
            headerBar
        },

        data : () => ({
            id          : null,
            password    : null,
            birth       : null,
            name        : null,
            textarea    : null,
            department  : null,
            call        : null,
            nickname    : null,
            selectedInterest : [],

            fail        : false,
            check       : false,

            passwordRules: [
                v => !!v || 'Name is required',
                v => (v && v.length >= 6) || 'password must be less than 10 characters'
            ],
            emailRules: [
                v => !!v || 'E-mail is required',
                v => /.+@.+/.test(v) || 'E-mail must be valid'
            ],
        }),
        
        methods:{
            signup:function(){
                this.$http.post("http://localhost:8000/user/signup",
                    {"ID":this.id, "password":this.password, "name":this.name, "department":this.department,
                     "nickname":this.nickname, "call":this.call, "interest": this.selectedInterest, "birth": this.birth}).
                then((res)=>{
                        console.log(res);

                        if(res.data.errmsg){
                            this.fail = true;
                            return res.status;
                }
                        else{
                            this.$router.push('/login');
                        }
                     })
            },

            onCheck: function() {
                this.signup()
            },
            onCancle: function(){
                return 0;
            }
        }

    }
</script>

<style lang="scss" scoped>
    .centered-container {
        display : flex;
        align-items: center;
        justify-content: center;
        position: relative;
        height: 120vh;

        .md-content {
            z-index: 0;
            padding: 150px;
            width: 100%;
            height : 200%;
            max-width: 1000px;
            max-height : 1000px;
            position: relative;

            .md-field {
                margin-bottom : 5%;
            }

            .actions {
                margin-top : 100px;

                .action-button{
                    width : 30%;
                    color : white;
                }
            }
        }
    }
</style>


