<template>
    <div class="circles">
        <!--   bar-->
        <header-bar></header-bar>

        <!--  container-->
        <div class="centered-container">
            <h1 class="jg ajou-title"> /  </h1>

            <!--    container (elevation : 10)-->
            <md-content class="md-elevation-15">

                <div class="content-end container-two" style="display : flex; justify-content : flex-end; flex-direction : row">

                    <v-form style="display : flex; justify-content: flex-end; flex-direction : row; width : 50%;">

                        <md-field>
                            <label> search_category</label>
                            <md-select v-model="search_select" name="search" id="search">
                                <md-option value="name"></md-option>
                                <md-option value="professor"></md-option>
                                <md-option value="president"></md-option>
                                <md-option value="subject"></md-option>
                            </md-select>
                        </md-field>

                        <v-text-field v-model="search_value" required style="margin-top : 0.9%; margin-left : 3%;width : 70%;"></v-text-field>

                        <v-btn @click="search()" style="width : 250px; margin-top : 1%;">  </v-btn>
                    </v-form>
                </div>

                <div class="container-one">
                <md-card v-for="circle in calData" :key="circle.id" class = "md-elevation-8">

                    <v-img v-if="circle.name === 'ANSI'"
                            src="https://cdn.vuetifyjs.com/images/cards/desert.jpg"
                            aspect-ratio="2.75"
                    ></v-img>

                    <img src="../../assets/A-dong.png" v-if="circle.name === ''" style="resize:both; max-height:268px; margin-left: 20%;">

                    <md-card-header>
                        <div class="md-title jg" style="font-size : 30px;">{{circle.name}}</div>
                    </md-card-header>

                    <md-card-content>
                        <p class="content-circle"> : {{circle.party}}</p>
                        <p v-if="circle.roomExistence == 1" class="content-circle"> : </p>
                        <p v-if="circle.roomExistence == 0" class="content-circle"> : </p>
                        <p class="content-circle">  : {{circle.president.name}}</p>
                        <p class="content-circle">  : {{circle.president.call}}</p>
                        <p v-if="circle.othersAccept === 1" class="content-circle">    : </p>
                        <p v-if="circle.othersAccept === 0" class="content-circle">    : </p>
                        <p class="content-circle">()  : {{circle.memberNumber}}</p>
                        <p class="content-circle">  :  {{circle.professor}}</p>
                        <p class="content-circle">  : {{circle.concept}}</p>
                        <p class="content-circle">  : {{circle.introduce}}</p>
                    </md-card-content>

                    <v-card-actions>
                        <v-btn color="blue-grey lighten-1" bottom  :to="'/circle/' + circle.name" style="width : 45%; margin : auto">
                            <p class="circle_button"> </p>
                        </v-btn>
                        <v-btn color="blue-grey lighten-1" bottom style="width : 45%; margin : auto">
                            <p class="circle_button" v-on:click="check=true; signcircle=circle"> </p>
                        </v-btn>
                    </v-card-actions>
                    <md-dialog-confirm
                        :md-active.sync="check"
                        md-title=" "
                        md-content="     ."
                        md-confirm-text="Check"
                        md-cancel-text="Cancle"
                        @md-cancel="onCancel"
                        @md-confirm="onCheck" />
                </md-card>
                </div>
            <md-dialog-confirm
                :md-active.sync="check"
                md-title="Check"
                md-content="  ?"
                md-confirm-text="Check"
                md-cancel-text="Cancle"
                @md-cancel="onCancel"
                @md-confirm="onCheck" />

                <div class="content-end container-two" style="display : flex; justify-items : center; flex-direction : column">
                    <v-pagination
                            v-model="curPage"
                            :length="numOfPages"
                            color="blue-grey darken-2"
                            bottom
                            next-icon="chevron_right"
                            prev-icon ="chevron_left"
                     ></v-pagination>
                </div>
            </md-content>

            <v-btn v-on:click="circleSignup()"> </v-btn>
        </div>
        <div>
            <footer-bar style="margin-top:17%"></footer-bar>
        </div>
    </div>
</template>

<script>
    import headerBar from '../../components/header'
    import footerBar from '../../components/footer'

    export default {
        name: 'circles',
        created (){
            this.$http.get('http://localhost:8000/circle/send').then((res) => {
                this.circles = res.data
            })
            this.userName = this.$session.getAll().username
            this.$http.get('http://localhost:8000/user/find/' + this.userName).then((res) => {
                this.user = res.data
            })
        },
        data() {
            return {
                userName: null,
                curPage : 1,
                dataPerPage : 6,
                food1 : null,

                search_value : '',
                search_category : ['name', 'professor'],
                search_select : null,

                items: ['Foo', 'Bar', 'Fizz', 'Buzz'],
                circles: [],
                signcircle: {},
                user: {},
                check: false
            }
        },

        components: {
            headerBar,
            footerBar,
        },

        computed: {
            numOfPages() {
                return Math.ceil(this.circles.length / this.dataPerPage);
            },
            startOffset() {
                return ((this.curPage - 1) * this.dataPerPage);
            },
            endOffset() {
                return (this.startOffset + this.dataPerPage);
            },
            calData() {
                return this.circles.slice(this.startOffset, this.endOffset)
            }
        },
        methods: {
            circleSignup(){
                this.$router.push('/circlesignup')
            },
            onCheck: async function() {
                await this.userSignup()
                this.check = false
            },
            onCancle: function(){
                this.check = false
            },
            userSignup: function() {
                this.$http.post('http://localhost:8000/user/' + this.user.name + '/signupCircle', this.signcircle)
                .then((res) => {

                    if(res.data !== "err"){
                        this.$http.post('http://localhost:8000/circle/' + this.signcircle.name + '/signupCircle/', this.user)
                    }
                    else{
                        alert("  .")
                    }
                })
            },
            search: function() {
                this.$http.post('http://localhost:8000/circle/send/search',
                    {"search_value": this.search_value, "search_select" : this.search_select})
                .then((res) => {
                    this.circles = res.data
                    this.search_value = ""
                    this.search_select = ""
                 })
            }

        }
    }
</script>

<style lang="scss" scoped>

    /*   container*/
    .centered-container {
        display: flex;
        align-items: center;
        flex-direction : column;
        padding : 5%;
        justify-content: center;
        position: relative;
        height: 80%;
        width : 100%;

        /* Title*/
        .ajou-title{
            margin-bottom : 2%;
            font-size : 40px;
            position : relative;
        }

        /*    container (elevation : 10)*/
        .md-content {
            z-index: 1;
            padding: 40px;
            width: 100%;
            height : 700%;
            max-width: 1700px;
            max-height : 3000px;
            position: relative;
            display : flex;
            justify-content : center;
            align-items: center;
            flex-wrap: wrap;
            flex-direction: column;

            .container-one {
                z-index: 1;
                padding: 15px;
                width: 100%;
                height : 300%;
                position: relative;
                display : flex;
                justify-content : center;
                align-items: center;
                flex-wrap: wrap;

                /*   card*/
                .md-card {
                    width: 45%;
                    height: 40%;
                    margin-left: 2%;
                    margin-bottom: 3%;
                    vertical-align: top;

                    /*( ), ( ) */
                    .circle_button {
                        color: white;
                        margin-top: 10px;
                    }

                    .content-circle {
                        font-size: 17px;
                    }
                }
            }

            .container-two {
                z-index: 1;
                padding: 10px;
                width: 100%;
                height : 100%;
                position: relative;
                display : flex;
                justify-content : center;
                align-items: center;
                flex-wrap: wrap;
            }
        }
    }

</style>



