<template>
    <div class="mypage">
        <!--   bar-->
        <header-bar></header-bar>

        <div id='all_notice'> </div>
        <!--  container-->
        <div class="centered-container">
            <h1 class="hn ajou_title"> MY PAGE </h1><br/>
            <!--   Login form box (elevation : 10)-->
            <md-content class="md-elevation-15">
            <form class="vue-form" @submit.prevent="submit">
                <v-layout row wrap>
                <md-card>
                    <md-card-header>
                        <div class="md-title">A-DONG </div>
                    </md-card-header>

                    <md-card-content>
                        <p>  : {{user.name}}</p>
                        <p> : {{user.nickname}}</p>
                        <p> : {{user.department}}</p>


                    <div id="c1" v-if="change1==true">
                    <md-field>
                        <label>Name</label>
                        <md-input v-model="name" placeholder=" "></md-input>
                    </md-field>
                    <md-field>
                        <label>Nickname</label>
                        <md-input v-model="nickname" placeholder=" "></md-input>
                    </md-field>
                    <md-field>
                        <label> </label>
                        <md-select v-model="department" name="department" id="department">
                            <md-optgroup label="">
                                <md-option value=""></md-option>
                                <md-option value="security"></md-option>
                                <md-option value="electric"></md-option>
                                <md-option value="media"></md-option>
                                <md-option value="Defense_digital"></md-option>
                            </md-optgroup>
                            <md-optgroup label="">
                                <md-option value="mechanical"></md-option>
                                <md-option value="Chemical"></md-option>
                                <md-option value="environmental"></md-option>
                                <md-option value="Advanced_Materials"></md-option>
                            </md-optgroup>
                            <md-optgroup label="">
                                <md-option value="mathematical"></md-option>
                                <md-option value="Physics"></md-option>
                            </md-optgroup>
                            <md-optgroup label="">
                                <md-option value="business"></md-option>
                                <md-option value="e-business">e-business </md-option>
                            </md-optgroup>
                            <md-optgroup label="">
                                <md-option value="korean"></md-option>
                                <md-option value="english"></md-option>
                            </md-optgroup>
                            <md-optgroup label="">
                                <md-option value="Psychology"></md-option>
                                <md-option value="Economics"></md-option>
                            </md-optgroup>
                            <md-optgroup label="">
                                <md-option value="medicine"></md-option>
                            </md-optgroup>
                            <md-optgroup label="">
                                <md-option value="nursing"></md-option>
                            </md-optgroup>
                            <md-optgroup label="">
                                <md-option value="Pharmacy"></md-option>
                            </md-optgroup>
                        </md-select>
                    </md-field>
                    </div>
                    </md-card-content>
                    <v-card-actions class="btn">
                        <v-btn round color="blue" large v-on:click="change1=true" v-if="change1==false">
                            <p class="circle_button"> </p>
                        </v-btn>                               
                    </v-card-actions>             
                    <v-card-actions class="c1">
                        <v-btn round color="blue" large v-on:click="change1=false" v-if="change1==true">
                            <p class="circle_button"> </p>
                        </v-btn>                               
                        <v-btn round color="blue" large v-on:click="changeProfile()" v-if="change1==true">
                            <p class="circle_button"> </p>
                        </v-btn>                  
                    </v-card-actions>
                </md-card>

                <md-card>
                    <md-card-header>
                        <div class="md-title"></div>
                    </md-card-header>

                    <md-card-content>
                        <p>Email : {{user.ID}}</p>
                        <p> : {{user.call}}</p>
                    </md-card-content>
                    <div v-if="change2==true">
                    <md-field>
                        <label>Email</label>
                        <md-input v-model="id" placeholder=" Email"></md-input>
                    </md-field>                    
                    <md-field>
                        <label>Call</label>
                        <md-input v-model="call" placeholder=" "></md-input>
                    </md-field>
                    </div>
                    <v-card-actions class="btn">
                        <v-btn round color="blue" large v-on:click="change2=true" v-if="change2==false">
                            <p class="circle_button"> </p>
                        </v-btn>                               
                    </v-card-actions>             
                    <v-card-actions class="c1">
                        <v-btn round color="blue" large v-on:click="change2=false" v-if="change2==true">
                            <p class="circle_button"> </p>
                        </v-btn>                               
                        <v-btn round color="blue" large v-on:click="changeCall()" v-if="change2==true">
                            <p class="circle_button"> </p>
                        </v-btn>                  
                    </v-card-actions>
                </md-card>
                </v-layout>


                <v-layout row wrap>                    
                <md-card>
                    <md-card-header>
                        <div class="md-title"> </div>
                    </md-card-header>

                    <md-card-content>
                        <div v-for="circle in user.circles" :key="circle.id" >
                        <p>
                              : {{circle.name}}
                            <v-btn round color="blue" large v-on:click="change3=false;circleout=circle;giveupCircle()" v-if="change3==true">
                                <p class="circle_button"></p>
                            </v-btn>                            
                        </p>
                        </div>                     
                    </md-card-content>
                    <v-card-actions class="btn">
                        <v-btn round color="blue" large v-on:click="change3=true" v-if="change3==false">
                            <p class="circle_button"> </p>
                        </v-btn>                               
                    </v-card-actions>             
                    <v-card-actions class="c1">
                        <v-btn round color="blue" large v-on:click="change3=false" v-if="change3==true">
                            <p class="circle_button"> </p>
                        </v-btn>                               
                        <v-btn round color="blue" large  v-if="change3==true">
                            <p class="circle_button"> </p>
                        </v-btn>                  
                    </v-card-actions>
                </md-card>
                <md-card>
                    <md-card-header>
                        <div class="md-title"> </div>
                    </md-card-header>
                    <md-field v-if="change4==true">
                        <label>Password </label>
                        <md-input v-model="password" type="password" placeholder=" "></md-input>
                        <v-btn round color="blue" large v-on:click="passwordCheck()" v-if="change4==true">
                            <p class="circle_button"> </p>
                        </v-btn>
                    </md-field>
                    <md-field v-if="change4==true">
                        <label>Change Password </label>
                        <md-input v-model="passwordin" type="password" placeholder=" "></md-input>
                    </md-field>

                    <md-field v-if="change5==true">
                        <label>Password </label>
                        <md-input v-model="password" type="password" placeholder=" ."></md-input>
                        <v-btn round color="blue" large v-on:click="passwordCheck()" v-if="change5==true">
                            <p class="circle_button"> </p>
                        </v-btn>
                    </md-field>
                    <p v-if="change4==true || change5 == true">{{passwordValue}}</p>

                    <v-card-actions class="btn">
                        <v-btn round color="blue" large v-on:click="change4=true" v-if="change4==false && change5==false">
                            <p class="circle_button"> </p>
                        </v-btn>
                        <v-btn round color="blue" large v-on:click="change4=false" v-if="change4==true">
                            <p class="circle_button"> </p>
                        </v-btn>                               
                        <v-btn round color="blue" large v-on:click="changePassword()" v-if="change4==true">
                            <p class="circle_button"> </p>
                        </v-btn> 
                        <v-btn round color="blue" large v-on:click="change5=false" v-if="change5==true">
                            <p class="circle_button"> </p>
                        </v-btn>                                         
                        <v-btn round color="red" large v-on:click="change5=true" v-if="change4==false&&change5==false">
                            <p class="circle_button"> </p>
                        </v-btn>
                        <v-btn round color="red" large v-on:click="deleteUser()" v-if="change5==true">
                            <p class="circle_button"> </p>
                        </v-btn>
                    </v-card-actions>
                </md-card>
                </v-layout>
            </form>
            </md-content>
        </div>
    </div>
</template>

<script>
import headerBar from '../../components/header.vue'

export default {
    data () {
        return {
            user: {},
            page : 1,
            userName: "",
            change1: false,
            change2: false,
            change3: false,
            change4: false,
            change5: false,
            id          : null,
            password    : null,
            birth       : null,
            name        : null,
            textarea    : null,
            department  : null,
            selectedInterest : [],
            call        : null,
            nickname    : null,
            birth       : null,
            passwordValue: null,
            passwordCorrect: false,
            passwordin: null,
            circleout: {},             
        }
    },
    created () {//     
        this.userName = this.$session.getAll().username
        this.$http.get('http://localhost:8000/user/find/' + this.userName).then((res) => {
            this.user = res.data
        })
    },
    name: 'mypage',
    components: {
        headerBar
    },
    methods:{
        passwordCheck: function(){
            this.passwordCorrect = false
            if(this.password == this.user.password){
                this.passwordValue = " ."
                this.passwordCorrect=true
            }
            else
                this.passwordValue = "  ."

        },
        changeProfile: function() {
            this.$http.post('http://localhost:8000/user/' + this.user.name + '/update/profile',
            {"name":this.name, "nickname":this.nickname, "department":this.department})
            .then((res) => {
                this.change1 = false
                this.$session.start()
                this.$session.set('username', res.data.name)
                this.$session.set('userDepartment', res.data.department)
                this.user = res.data
                this.$router.go(0)     
            })
        },
        changeCall: function() {
            this.$http.post('http://localhost:8000/user/' + this.user.name + '/update/call',
            {"call":this.call, "ID":this.id})
            .then((res) => {
                this.change2 = false
                this.$session.start()
                this.$session.set('username', res.data.name)
                this.$session.set('userDepartment', res.data.department)
                this.user = res.data
                this.$router.go(0)     
            })            
        },
        changePassword: function() {
            if(this.passwordCorrect==true){
                this.$http.post('http://localhost:8000/user/' + this.user.name + '/update/password',
                {"password":this.passwordin})
                .then((res) => {
                    this.change4 = false
                    this.$session.start()
                    this.$session.set('username', res.data.name)
                    this.$session.set('userDepartment', res.data.department)
                    this.user = res.data
                    this.$router.go(0)     
                })
            }
        },
        deleteUser: function() {
            if(this.passwordCorrect==true){
                this.$http.delete('http://localhost:8000/user/' + this.user.name + '/delete')
                .then((res) => {
                    this.$session.destroy();
                    this.$router.push('/');
                    window.location.reload();                    
                })
            }
        },
        giveupCircle: function(){
            this.$http.post('http://localhost:8000/circle/'+this.circleout.name+'/reject', this.user).then(() => {
                this.$http.post('http://localhost:8000/user/'+this.user.name+'/reject', this.circleout)
            }).then(() => {
                this.$router.go(0)
            })
        }    
    }
}
</script>

<style lang="scss" scoped>

    .centered-container {
        display: flex;
        align-items: center;
        flex-direction : column;
        justify-content: center;
        position: relative;
        height: 125vh;
        .md-card {
            width : 600px;
            height : 500px;
            margin-left : 50px;
            margin-bottom: 50px;
            vertical-align: top;

            .md-button{
                margin-bottom : 10px;
                width : 500px;
            }
        }

        .md-content {
            padding: 40px;
            width: 100%;
            height : 200%;
            max-width: 1700px;
            max-height : 1200px;
            position: relative;
            display : flex;
            justify-content : center;
            flex-wrap: wrap;
        }
    }

    .circle_button{
        color : white;
        margin-top : 10px;
    }

    .ajou_title{
        margin-bottom : 30px;
        font-size : 40px;
    }

    #first_row {
        color : red;
    }

    #all_notice {
        margin-bottom : 100px;

    }
    .md-title {
        text-align: center;
        font-size: 30px;
        font-weight: 700;
        margin-bottom: 30px;
    }
    .md-card-content {
        font-size: 20px;
    }
    .btn{
        position: absolute;
        bottom: 0%;
        
        width: 25%;
    }
    .c1{
        position: absolute;
        bottom: 0%;
    }
</style>


