<template>
    <div class="signup">
        <!--   bar-->
        <header-bar></header-bar>

        <!--  container-->
        <div class="centered-container">
            <!--   Login form box (elevation : 10)-->

            <md-content class="md-elevation-10" >
                <!--Title  +  -->
                <div class="title">
                    <router-link to="/">
                    <img src="../../assets/A-dong.png">
                    </router-link>
                </div>

                <div class="login-form">
                    <!--ID -->
                    <md-field>
                        <label>ID</label>
                        <md-input v-model="login.ID" autofocus></md-input>
                    </md-field>
                    <!--password -->
                    <md-field md-has-password>
                        <label>Password</label>
                        <md-input v-model="login.password" type="password" ></md-input>
                    </md-field>
                </div>

                <div class="actions md-layout md-alignment-center-space-around">
                    <a to="/"> </a>
                    <a to="/"> </a>
                </div>

                <div class="form-actions md-layout md-alignment-center-space-around">
                    <v-btn class="md-raised md-primary jg" color="blue-grey lighten-1" v-on:click="auth"></v-btn>
                </div>

                <div class="form-actions md-layout md-alignment-center-space-around">
                    <v-btn class="md-raised md-primary jg" color="blue-grey lighten-1" to="/signup"> </v-btn>

                </div>
            </md-content>
            
        </div>
        <!--alert operation -->
        <md-dialog-alert
            :md-active.sync="active"
            md-content="   ."
            md-confirm-text="" />

        <md-divider></md-divider>
    </div>
</template>

<script>
    import headerBar from '../../components/header'

    export default {
        name: 'login',
        data: function(){
            return {
                loading : false,
                login: {
                    ID: '',
                    password: ''
                },
                active  : false
            };
        },
        components: {
            headerBar
        },
        methods: {
            auth: function() {
                this.$http.post("http://localhost:8000/user/signin",{"ID":this.login.ID,"password":this.login.password}).then((res)=>{
                    if(res.data.ID==null){
                        this.active=true
                        return;
                    }
                    else if(res.data.auth===false){
                        this.active=true
                        return;
                    }
                    this.$session.start()
                    this.$session.set('username', res.data.name)
                    this.$session.set('userDepartment', res.data.department)
                    
                    this.$router.push('/')
                })
            }
        }
    };
</script>

<style lang="scss">
    .centered-container { //  Container
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        height: 100vh;


        .md-content { // Box
            z-index: 1;
            padding: 40px;
            width: 100%;
            height : 200%;
            max-width: 500px;
            max-height : 600px;
            position: relative;

            .title { // A-Dong + img
                text-align: center;
                margin-bottom: 30px;
                img {
                    resize: both;
                    max-width :300px;
                    height : 150px;
                    margin-left : 10%
                }
                .md-title{ // A-Dong
                    font-size : 50px;
                }
            }

            .login-form { // id  
                margin-bottom: 15%;
            }

            .form-actions { // / 
                margin-bottom : 3%;

                .md-button { //  , 
                    width : 500px;
                }
                .v-btn { //  , 
                    width : 400px;
                    color : white;
                }
            }
        }

    }
</style>



