<template>
    <div class="circlesignup">
        <header-bar></header-bar>
        <div class="centered-container">
            <md-content class="md-elevation-10">
                <form class="vue-form" @submit.prevent="submit">
                    <md-field>
                        <label> </label>
                        <md-input v-model="name"></md-input>
                    </md-field>
                       <div class="md-layout-item">
                    <md-field>
                        <label> </label>
                            <md-select v-model="department" name="department" id="department">
                            <md-optgroup label="">
                                <md-option value="software"></md-option>
                                <md-option value="security"></md-option>
                                <md-option value="electric"></md-option>
                                <md-option value="media"></md-option>
                                <md-option value="Defense_digital"></md-option>
                            </md-optgroup>
                            <md-optgroup label="">
                                <md-option value="mechanical"></md-option>
                                <md-option value="Chemical"></md-option>
                                <md-option value="environmental"></md-option>
                                <md-option value="Advanced_Materials"></md-option>
                            </md-optgroup>
                            <md-optgroup label="">
                                <md-option value="mathematical"></md-option>
                                <md-option value="Physics"></md-option>
                            </md-optgroup>
                            <md-optgroup label="">
                                <md-option value="business"></md-option>
                                <md-option value="e-business">e-business </md-option>
                            </md-optgroup>
                            <md-optgroup label="">
                                <md-option value="korean"></md-option>
                                <md-option value="english"></md-option>
                            </md-optgroup>
                            <md-optgroup label="">
                                <md-option value="Psychology"></md-option>
                                <md-option value="Economics"></md-option>
                            </md-optgroup>
                            <md-optgroup label="">
                                <md-option value="medicine"></md-option>
                            </md-optgroup>
                            <md-optgroup label="">
                                <md-option value="nursing"></md-option>
                            </md-optgroup>
                            <md-optgroup label="">
                                <md-option value="Pharmacy"></md-option>
                            </md-optgroup>
                        </md-select>
                    </md-field>
                </div>
                </form>
                <form>
                    <md-field>
                        <label> </label>
                        <md-input v-model="professor" placeholder=" "></md-input>
                    </md-field> 
                    <md-field>
                        <label></label>
                        <md-input v-model="presidentin" placeholder=" "></md-input>
                    </md-field>                    
                    <md-button class="md-raised md-primary md-alignment-center" v-on:click="find()"></md-button> 
                </form>
                    {{this.president.ID}}
                    {{exist}}
                    <md-field>
                        <label></label>
                        <md-input v-model="number" type="number"></md-input>
                    </md-field>
                    <md-field>
                        <label> </label>
                        <md-input v-model="roomExistence" placeholder="1 or 0"></md-input>
                    </md-field> 
                    <md-field>
                        <label>   </label>
                        <md-input v-model="othersAccept" placeholder="1 or 0"></md-input>
                    </md-field> 
                    <md-field>
                    <label></label>
                    <md-select v-model="concept" multiple>
                        <md-option value="music"></md-option>
                        <md-option value="study">(Study)</md-option>
                        <md-option value="exercise"></md-option>
                        <md-option value="dance"></md-option>
                        <md-option value="movie"> & </md-option>
                        <md-option value="trip"></md-option>
                        <md-option value="contest"></md-option>
                    </md-select>
                    </md-field>

                    <md-field>
                        <label></label>
                        <md-textarea v-model="introduce"></md-textarea>
                    </md-field>   
                    <div class="actions md-layout md-alignment-center">
                        <md-button class="md-raised md-primary md-alignment-center" v-on:click="check=true"></md-button>
                        <md-button class="md-raised md-primary" href="/"></md-button>
                    </div>
            </md-content>
        </div>
        <md-dialog-confirm
        :md-active.sync="check"
        md-title="Check"
        md-content="  ?"
        md-confirm-text="Check"
        md-cancel-text="Cancle"
        @md-cancel="onCancel"
        @md-confirm="onCheck" />
    </div>
</template>
<script>
import headerBar from '../../components/header'
    
export default {
    name: 'circlesignup',
    data () {
        return {
            circle: {},
            president: {},
            exist: "",
            name: null,
            autogrow: null,
            check: false,
            concept: [],
        }
    },
    components: {
        headerBar
    },
    methods: {
        register: function () {
            this.circle = {name: this.name, party: this.department, memberNumber: this.number
            , concept: this.concept, introduce: this.introduce, president: this.president, professor: this.professor
            , roomExistence: this.roomExistence, othersAccept: this.othersAccept}
            this.$http.post('http://localhost:8000/circle/register', this.circle) 
        },
        onCheck: async function() {
            await this.register()
            await this.$router.push('/circles')
        },
        onCancle: function() {
            this.check = false
        },
        find: function () {
            this.$http.get('http://localhost:8000/user/find/' + this.presidentin).then((res) => {
                this.president = res.data
            }).then(() => {
                if(this.president)
                    this.exist = "    ."
                else
                    this.exist = "   ."
            })
        }
    }

}
</script>

<style lang="scss" scoped>

    .centered-container {

        display : flex;
        align-items: center;
        justify-content: center;
        position: relative;
        height: 120vh;

        .md-content {
            z-index: 0;
            padding: 150px;
            width: 100%;
            height : 200%;
            max-width: 1000px;
            max-height : 1000px;
            position: relative;

            .actions {
                margin-top : 100px;
            }
        }
    }

</style>


